import React from "react";
import { useParams } from "react-router-dom";
import { Header } from "../../../shared/components/Header/Header";
import s from "./NewsPage.module.css";

const NewsPage = () => {
  const { id } = useParams(); // Получаем параметр id из URL
  const newsPages = [
    {
      id: "1",
      title: "Austyn Gillette’s “Know You My Own Way” Globe Skateboarding Part",
      text: "Остин вкладывает всю душу, смешивая мощные линии, безупречную форму и новаторские движения. От удара гидранта до финала, этот глобальный разрыв — высший класс.",
      videoLink: "https://www.youtube.com/embed/ror6kRSUQZo",
    },
    {
      id: "2",
      title: "Jante – 6:33",
      text: "Фритте: «Может быть... Да, возможно, лучшая поездка в Джанте на данный момент. Местные жители все дружелюбны, еда действительно вкусная и дешевая, и места везде».Съемочной группе не пришлось идти дальше, чем в 20 минутах от хостела, и все же, как вы можете видеть по фотографиям, самые крутые места были везде. Места, которые они никогда раньше не видели. Если что, они использовали приложение Shinner (на самом деле), и Дэвид Сода, местный гид по местам, мог сосредоточиться на том, чтобы познакомить съемочную группу с лучшими местами для ужина.Все так просто? Вы можете спросить. Достаточно ли хорошей еды и съемок скейтеров в крутых местах? А как насчет остальной Сербии? Ну, я ясно сказал вам в начале: Фритте — человек, стоящий за объективом того клипа, который вы помните. А не тот антрополог-историк, о котором вы никогда не слышали.Еда, места, отличное катание, облажались со съемками на одной из линий Тайлера и Джакинда, бегло говорящая по-сербски, — вот, в общем-то, все, что Фритте рассказал мне о поездке. Думаю, нам просто нужно снять клип, и фотографии говорят сами за себя.В ресторане постоянно слышен ропот пыльных людей. «Я могу снять еще один», — говорит Фритте официантке и указывает на пустой стакан перед ним.Но быть единственным оператором в таких поездках — это тяжелая работа. Особенно если востребованы линии «рыбий глаз». Когда один скейтер получает свой клип и может, наконец, сесть и отдохнуть, Фритте приходится начинать снимать следующий.«Это, через некоторое время, в сочетании с испорченной съемкой клипа, может действительно испортить мое настроение... и мои чувства».",
      videoLink: "https://www.youtube.com/embed/X6AQ6a42efM",
    },
    {
      id: "3",
      title: "BLUE - Rassvet",
      text: "Последний блестяще смонтированный полнометражный фильм, снятый в Париже и Лос-Анджелесе (с небольшими вкраплениями Барселоны и Мексики), чтобы поприветствовать Реми Тавейру и Остина Жилетта в своем квартале!",
      videoLink: "https://www.youtube.com/embed/bbCqyWAXCBs",
    },
  ];

  // Находим новость по id
  const currentNews = newsPages.find((item) => item.id === id);

  if (!currentNews) {
    return <h2>Новость не найдена</h2>;
  }

  return (
    <div className={s.newsContainer}>
      <Header />
      <div className={s.newsItem}>
        <h2 className={s.newsTitle}>{currentNews.title}</h2>
        <iframe
          width="560"
          height="315"
          src={currentNews.videoLink}
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
        <p className={s.newsText}>{currentNews.text}</p>
      </div>
    </div>
  );
};

export default NewsPage;
